.App {
    min-height: 100vh;
    background-image: linear-gradient( to right, #ff8177 0%, #ff867a 0%, #ff8c7f 21%, #f99185 52%, #cf556c 78%, #b12a5b 100%);
}

.search-form {
    min-height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search-bar {
    border: 3px solid transparent;
    height: 40px;
    width: 50%;
    border-radius: 50px;
    padding: 10px;
    margin-right: 1rem;
    outline: none;
}

.card-img-top {
    border-radius: 50%;
    margin-top: 1rem;
}

.card-title {
    /* color: ; */
    font-size: x-large;
    justify-content: center;
}

#card {
    padding: 0.5rem;
    box-shadow: 0px 5px 20px black;
}